<template>
    <el-col :span="21">
        <el-tabs :value="active"  @tab-click="handleClick">
            <el-tab-pane  v-for="item in data" :key="item.seq" :label="item.label" :name="item.name">
                <template v-for="children in item.children">
                    <div :key="children.value" class="children" @click="toggleChoose(children)">
                        {{children.label}}
                    </div>
                </template>
            </el-tab-pane>
        </el-tabs>
    </el-col>
</template>

<script>
    export default {
        name: "tabs",
        props: ["data","active"],
        data() {
            return {
            }
        },
        mounted() {

        },
        methods: {
            handleClick(tab) {
                console.log(tab.name)
            },
            toggleChoose: function(item){
                console.log(item);
                let obj = {
                    value: item.value,
                    label: item.label
                }
                //let selectedList = window.localStorage.getItem("selected");
                window.localStorage.setItem("selected", obj);
            }
        }
    }
</script>

<style scoped>
    .children{
        float: left;
        height: 40px;
        width: 200px;
        border: gray 1px solid;
        margin: 5px;
        text-align: center;
        line-height: 40px;
    }
</style>